import React from 'react';

class Navigation extends React.Component {
    constructor() {
        super()

        this.wrapper = React.createRef();
        this.state = {
            height: '',
            position: 0,
            links: {
                men: 'For men',
                women: 'For women',
                kids: 'For kids'
            }
        }

        this.fixed = this
            .fixed
            .bind(this)

            
        
    }

    fixed() {

        const limit = window.scrollY + 120;
        if (limit > window.innerHeight) {
            this.setState({height: 'navtop'})
        } else {
            this.setState({height: ''})
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.fixed)

    }


    render() {
        const {height, links} = this.state
        
        return (
            <div ref={this.wrapper} className={`row bg ${this.props.bgcolor} ${height}`}>
                <div
                    className="offset-9 col-7 offset-s-3 col-s-19 align-center justify-between">
                    <a className={`navigation-link light ${this.props.hide1}`} onClick={this.props.eventmen} href="#" >{links.men}</a>
                    <a className={`navigation-link light ${this.props.hide2}`}  onClick={this.props.eventwomen} href="#">{links.women}</a>
                    <a className={`navigation-link light ${this.props.hide3}`}  onClick={this.props.eventkids} href="#">{links.kids}</a>
                </div>
            </div>
        )
    }
}

export default Navigation