import React from 'react'
import Nav from './Nav';
import Header from './Header';
import Tile from './Tile'
import ProductTile from './ProductTile'
import Title from './Title'
import video from './videos/video.mp4';
import video2 from './videos/video2.mp4';
import videokids from './videos/videokids.mp4';
import videomen from './videos/videomen.mp4';
import videowomen from './videos/videowomen.mp4';
import videowomen2 from './videos/videowomen2.mp4';
import men from './images/men.jpg'
import women from './images/women.jpg'
import kids from './images/kids.jpg'
import image2 from './images/image2.jpg'
import bag1 from './images/bag1.jpg'
import bag2 from './images/bag2.jpg'
import giftmen1 from './images/giftmen1.jpg'
import giftmen2 from './images/giftmen2.jpg'
import Sticky from './Sticky'
import Parallax from './Parallax'
import Supercomponent from './Supercomponent'

class Container extends React.Component {
    constructor() {
        super()
        this.node = React.createRef()
        this.state = {
            img1: bag1,
            img2: bag2,
            videos: video,
            title: "Men's gift guide",
            hide1: '',
            hide2: '',
            hide3: '',
            bgcolor: 'red'
        }
        this.handleClick = this
            .handleClick
            .bind(this)

        this.changeCatMen = this
            .changeCatMen
            .bind(this)

        this.changeCatWomen = this
            .changeCatWomen
            .bind(this)

        this.changeCatKids = this
            .changeCatKids
            .bind(this)
    }

    handleClick() {
        this.setState({img1: giftmen1, img2: giftmen2})
    }

    changeCatMen(e) {
        this.setState({videos: videomen, title: "Men's gift guide", hide1: 'none', hide2: '', hide3: '', bgcolor: 'green'})
    }

    changeCatWomen(e) {
        e.preventDefault();
        this.setState({videos: videowomen2, title: "Women's gift guide", hide1: '', hide2: 'none', hide3: '', bgcolor: 'pink'})
    }

    changeCatKids(e) {
        e.preventDefault();
        this.setState({videos: videokids, title: "Kids gift guide", hide1: '', hide2: '', hide3: 'none', bgcolor: 'blue'})
    }

    render() {
        const {
            hey,
            img1,
            img2,
            videos,
            title,
            hide1,
            hide2,
            hide3,
            bgcolor
        } = this.state

        const tiles = [1, 2, 3, 4, 5];
        const three = "offset-1 col-7 col-s-23 mgbt-1 mgbt-m-2 flex-col full hide";
        const two = "offset-1 col-11 col-s-23 mgbt-1 mgbt-m-2 flex-col full hide";

        return (

            <div ref={this.node} className="row">
                <Nav/>
                <Header
                    eventmen={this.changeCatMen}
                    eventwomen={this.changeCatWomen}
                    eventkids={this.changeCatKids}
                    video={videos}
                    url={video}
                    title={title}
                    hide1={hide1}
                    hide2={hide2}
                    hide3={hide3}
                    bgcolor={bgcolor}/>

                <Title
                    title="Gifts for him"
                    text="Lorem ipsum Nam Donec malesuada leo,
        vehicula commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque
        Cras sed vitae eget Morbi rhoncus feugiat ultricies eget lectus erat Sed elit.
        Erat, a Nam sagittis nisi vitae arcu. Turpis lorem, Donec ac eget metus risus.
        Justo, sollicitudin lectus tempor lacinia."/>
                <Parallax event={this.handleClick} img1={img1} img2={img2}/>
                <Tile
                    classname={three}
                    title="For him"
                    cta="All products"
                    img={men}
                    alt="image"/> {tiles.map(tile => <ProductTile key={tile} img={image2} alt="image"/>)}
                <Tile
                    classname={two}
                    title="For her"
                    cta="All products"
                    img={women}
                    alt="image"/>
                <Tile
                    classname={two}
                    title="For kids"
                    cta="All products"
                    img={kids}
                    alt="image"/>

            </div>
        )
    }
}

export default Container